import React, { useEffect } from 'react'
import GotoMixIcon from '../assets/Next_Normal.svg'
import GotoMixIconDis from '../assets/next-disabel.svg'
import { throttle, InitializationThrottle } from '../utils/throttle'
export default function GoToNext (props) {
  const { disabled, onClick, style } = props
  useEffect(() => {
    InitializationThrottle()
  }, [])
  return (
    <div onClick={disabled ? null : () => throttle(onClick)}>
      <img
        src={disabled ? GotoMixIconDis : GotoMixIcon}
        className='GotoMixIcon'
        alt=''
        style={{
          position: 'absolute',
          right: 12,
          top: 2,
          zIndex: 11,
          width: 40,
          height: 40,
          transition: 'all .4s',
          ...style
        }} />
    </div>
  )
}
